import React from "react";
import { ReactComponent as DouzoneWhite } from "../../../../assets/lg-douzone-white.svg";
import { useNavigate } from "react-router";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import __ from "lodash";

import {
  RESET_AUTHSLICE,
  SET_LOGOUT,
} from "../../../../store/features/authSlice";
import { toast } from "react-toastify";

function LayoutHeader() {
  const { isAuth, roles } = useSelector((state) => state.auth);
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const onLogout = () => {
    dispatch(SET_LOGOUT());
    // 성공 실패여부 상관없이
    dispatch(RESET_AUTHSLICE());
    navigate("/", { replace: true });
    toast.success("로그아웃");
  };

  const onLogin = () => {
    navigate("auth/login", { replace: true });
  };

  const onAdmin = () => {
    navigate("admin", { replace: true });
  };

  return (
    <>
      <nav className="header_main">
        <Link className="col-span-4" to={"/"}>
          <DouzoneWhite />
        </Link>
        {__.find(roles, (item) => {
          return item === "ROLE_ADMIN";
        }) ? (
          <div className="col-span-1 text-white" onClick={onAdmin}>
            관리자 모드
          </div>
        ) : (
          <div className="col-span-1" />
        )}
        {isAuth ? (
          <div className="col-span-1 text-right text-white">
            <div onClick={onLogout}>로그아웃</div>
          </div>
        ) : (
          <div className="col-span-1 text-right text-white">
            <div onClick={onLogin}>로그인</div>
          </div>
        )}
      </nav>
    </>
  );
}

export default LayoutHeader;
